/* ==================================================================================
   Body
   ================================================================================== */
body {
  margin: 0;
  overflow-y: scroll;
}
/* ==================================================================================
   CKEditor
   ================================================================================== */
div.cke_float[id*="cke_keditor-component-content-"] {
  padding: 0;
}
div.cke_float[id*="cke_keditor-component-content-"] .cke_top {
  padding: 3px 3px 0;
}
/* ==================================================================================
   opened-keditor-sidebar
   ================================================================================== */
body.opened-keditor-sidebar .keditor-content-area {
  margin-right: 260px;
}
body.opened-keditor-sidebar div.cke_float[id*="cke_keditor-component-content-"] {
  margin-right: 287px;
}
body.opened-keditor-sidebar #keditor-sidebar {
  right: 0;
}
/* ==================================================================================
   highlighted-container-content
   ================================================================================== */
body.highlighted-container-content .keditor-container-content {
  outline-color: #2eb2e4;
}
/* ==================================================================================
   Content area
   ================================================================================== */
.keditor-content-area {
  min-height: 100px;
  width: auto;
  margin: 0;
  padding: 80px 50px 30px;
}
/* ==================================================================================
   Sidebar
   ================================================================================== */
#keditor-sidebar {
  position: fixed;
  z-index: 1000;
  top: 0;
  right: -255px;
  bottom: 0;
  background: #f0f0f0;
  opacity: .9;
  border-left: 1px solid #ddd;
  width: 255px;
  box-shadow: -1px 0 5px rgba(0, 0, 0, 0.15);
  padding: 10px;
  -webkit-transition: all 0.3s;
  -o-transition: all 0.3s;
  transition: all 0.3s;
  font: 14px/1.42857143 "Helvetica Neue", Helvetica, Arial, sans-serif;
}
#keditor-sidebar,
#keditor-sidebar *,
#keditor-sidebar:before,
#keditor-sidebar *:before,
#keditor-sidebar:after,
#keditor-sidebar *:after {
  box-sizing: border-box;
}
#keditor-sidebar:hover {
  opacity: 1;
}
#keditor-sidebar .nav {
  margin-bottom: 0;
  padding-left: 0;
  list-style: none;
}
#keditor-sidebar .nav > li {
  position: relative;
  display: block;
}
#keditor-sidebar .nav > li > a {
  position: relative;
  display: block;
  padding: 10px 15px;
}
#keditor-sidebar .nav > li > a:hover,
#keditor-sidebar .nav > li > a:focus {
  text-decoration: none;
  background-color: #eeeeee;
}
#keditor-sidebar .nav > li.disabled > a {
  color: #777777;
}
#keditor-sidebar .nav > li.disabled > a:hover,
#keditor-sidebar .nav > li.disabled > a:focus {
  color: #777777;
  text-decoration: none;
  background-color: transparent;
  cursor: not-allowed;
}
#keditor-sidebar .nav .open > a,
#keditor-sidebar .nav .open > a:hover,
#keditor-sidebar .nav .open > a:focus {
  background-color: #eeeeee;
  border-color: #337ab7;
}
#keditor-sidebar .nav .nav-divider {
  height: 1px;
  margin: 9px 0;
  overflow: hidden;
  background-color: #e5e5e5;
}
#keditor-sidebar .nav > li > a > img {
  max-width: none;
}
#keditor-sidebar .nav-tabs {
  border-bottom: 1px solid #ddd;
}
#keditor-sidebar .nav-tabs > li {
  float: left;
  margin-bottom: -1px;
}
#keditor-sidebar .nav-tabs > li > a {
  margin-right: 2px;
  line-height: 1.42857143;
  border: 1px solid transparent;
  border-radius: 4px 4px 0 0;
}
#keditor-sidebar .nav-tabs > li > a:hover {
  border-color: #eeeeee #eeeeee #ddd;
}
#keditor-sidebar .nav-tabs > li.active > a,
#keditor-sidebar .nav-tabs > li.active > a:hover,
#keditor-sidebar .nav-tabs > li.active > a:focus {
  color: #555555;
  background-color: #fff;
  border: 1px solid #ddd;
  border-bottom-color: transparent;
  cursor: default;
}
#keditor-sidebar .nav-tabs.nav-justified {
  width: 100%;
  border-bottom: 0;
}
#keditor-sidebar .nav-tabs.nav-justified > li {
  float: none;
}
#keditor-sidebar .nav-tabs.nav-justified > li > a {
  text-align: center;
  margin-bottom: 5px;
}
#keditor-sidebar .nav-tabs.nav-justified > .dropdown .dropdown-menu {
  top: auto;
  left: auto;
}
@media (min-width: 768px) {
  #keditor-sidebar .nav-tabs.nav-justified > li {
    display: table-cell;
    width: 1%;
  }
  #keditor-sidebar .nav-tabs.nav-justified > li > a {
    margin-bottom: 0;
  }
}
#keditor-sidebar .nav-tabs.nav-justified > li > a {
  margin-right: 0;
  border-radius: 4px;
}
#keditor-sidebar .nav-tabs.nav-justified > .active > a,
#keditor-sidebar .nav-tabs.nav-justified > .active > a:hover,
#keditor-sidebar .nav-tabs.nav-justified > .active > a:focus {
  border: 1px solid #ddd;
}
@media (min-width: 768px) {
  #keditor-sidebar .nav-tabs.nav-justified > li > a {
    border-bottom: 1px solid #ddd;
    border-radius: 4px 4px 0 0;
  }
  #keditor-sidebar .nav-tabs.nav-justified > .active > a,
  #keditor-sidebar .nav-tabs.nav-justified > .active > a:hover,
  #keditor-sidebar .nav-tabs.nav-justified > .active > a:focus {
    border-bottom-color: #fff;
  }
}
#keditor-sidebar .nav-justified {
  width: 100%;
}
#keditor-sidebar .nav-justified > li {
  float: none;
}
#keditor-sidebar .nav-justified > li > a {
  text-align: center;
  margin-bottom: 5px;
}
#keditor-sidebar .nav-justified > .dropdown .dropdown-menu {
  top: auto;
  left: auto;
}
@media (min-width: 768px) {
  #keditor-sidebar .nav-justified > li {
    display: table-cell;
    width: 1%;
  }
  #keditor-sidebar .nav-justified > li > a {
    margin-bottom: 0;
  }
}
#keditor-sidebar .nav-tabs-justified {
  border-bottom: 0;
}
#keditor-sidebar .nav-tabs-justified > li > a {
  margin-right: 0;
  border-radius: 4px;
}
#keditor-sidebar .nav-tabs-justified > .active > a,
#keditor-sidebar .nav-tabs-justified > .active > a:hover,
#keditor-sidebar .nav-tabs-justified > .active > a:focus {
  border: 1px solid #ddd;
}
@media (min-width: 768px) {
  #keditor-sidebar .nav-tabs-justified > li > a {
    border-bottom: 1px solid #ddd;
    border-radius: 4px 4px 0 0;
  }
  #keditor-sidebar .nav-tabs-justified > .active > a,
  #keditor-sidebar .nav-tabs-justified > .active > a:hover,
  #keditor-sidebar .nav-tabs-justified > .active > a:focus {
    border-bottom-color: #fff;
  }
}
#keditor-sidebar .tab-content > .tab-pane {
  display: none;
}
#keditor-sidebar .tab-content > .active {
  display: block;
}
#keditor-sidebar-toggler {
  position: absolute;
  top: 100px;
  width: 25px;
  height: 30px;
  left: -25px;
  background: #f0f0f0;
  text-align: center;
  line-height: 30px;
  border: 1px solid #ddd;
  border-right: 0;
  cursor: pointer;
  border-radius: 3px 0 0 3px;
  color: #666;
}
/* ==================================================================================
   Snippets
   ================================================================================== */
#keditor-snippets-list {
  height: 100%;
}
#keditor-snippets-list .keditor-snippet:hover {
  opacity: .6;
  border-color: #ccc;
}
#keditor-snippets-type-switcher {
  margin: 0;
}
#keditor-snippets-container {
  height: calc(100% - 42px);
  background: #fff;
  padding: 10px 5px;
  border: solid #ddd;
  border-width: 0 1px 1px;
}
#keditor-snippets-container > .keditor-snippets {
  height: 100%;
  overflow-y: auto;
  overflow-x: hidden;
  -webkit-transition: opacity 0.3s;
  -o-transition: opacity 0.3s;
  transition: opacity 0.3s;
}
#keditor-snippets-container > .keditor-snippets:before,
#keditor-snippets-container > .keditor-snippets:after {
  content: " ";
  display: table;
}
#keditor-snippets-container > .keditor-snippets:after {
  clear: both;
}
.keditor-snippet {
  padding: 5px;
  border: 1px solid #f0f0f0;
  cursor: move;
  -webkit-transition: all 0.3s;
  -o-transition: all 0.3s;
  transition: all 0.3s;
  float: left;
  background: #fff;
  margin: 0 5px 10px;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
.keditor-snippet img {
  display: block;
}
.keditor-snippet:last-child {
  margin-bottom: 0;
}
/* ==================================================================================
   Toolbar
   ================================================================================== */
.keditor-toolbar {
  position: absolute;
  display: none;
}
.keditor-toolbar,
.keditor-toolbar *,
.keditor-toolbar:before,
.keditor-toolbar *:before,
.keditor-toolbar:after,
.keditor-toolbar *:after {
  box-sizing: border-box;
}
.keditor-toolbar > a {
  padding: 1px 3px;
  font-size: 12px;
  width: 20px;
  height: 20px;
  display: block;
  text-align: center;
  line-height: 20px;
}
.keditor-toolbar .btn-container-reposition,
.keditor-toolbar .btn-component-reposition {
  cursor: move;
}
/* ==================================================================================
   Container
   ================================================================================== */
.keditor-container {
  position: relative;
  outline: 3px dashed transparent;
  outline-offset: 0;
}
.keditor-container > .keditor-toolbar {
  top: -3px;
  left: -39px;
  border: 1px solid #333;
  border-radius: 3px;
  overflow: hidden;
}
.keditor-container > .keditor-toolbar > a {
  background: #333;
  color: #fff;
}
.keditor-container > .keditor-toolbar > a:hover {
  background: #2eb2e4;
}
.keditor-container:hover {
  outline-color: #b5e4f6;
}
.keditor-container.showed-keditor-toolbar {
  outline-color: #2eb2e4;
}
.keditor-container.showed-keditor-toolbar > .keditor-toolbar {
  display: block;
}
.keditor-container-content {
  min-height: 50px;
  outline: 1px dashed #e0e0e0;
  outline-offset: -1px;
}
/* ==================================================================================
   Component
   ================================================================================== */
.keditor-component {
  position: relative;
}
.keditor-component .keditor-toolbar {
  top: 3px;
  right: 3px;
  left: 3px;
  background-color: rgba(0, 0, 0, 0.5);
  padding-bottom: 3px;
  text-align: right;
}
.keditor-component .keditor-toolbar > a {
  border: 0;
  background: none;
  color: #fff;
  text-align: center;
  border-radius: 0;
  -webkit-box-shadow: none;
  box-shadow: none;
  -webkit-transition: all 0.3s;
  -o-transition: all 0.3s;
  transition: all 0.3s;
  display: inline-block;
}
.keditor-component .keditor-toolbar > a:hover {
  background-color: rgba(255, 255, 255, 0.2);
}
.keditor-component:hover,
.keditor-component.showed-keditor-toolbar {
  outline: 3px solid rgba(0, 0, 0, 0.5);
  outline-offset: -3px;
}
.keditor-component.showed-keditor-toolbar .keditor-component-content {
  outline: none !important;
}
.keditor-component:hover .keditor-toolbar {
  display: block;
}
/* ==================================================================================
   Drag n Drop helper
   ================================================================================== */
.keditor-snippet,
.keditor-snippet *,
.keditor-snippet:before,
.keditor-snippet *:before,
.keditor-snippet:after,
.keditor-snippet *:after {
  box-sizing: border-box;
}
.keditor-snippet.ui-draggable-dragging {
  -webkit-transition: none !important;
  -o-transition: none !important;
  transition: none !important;
}
.ui-sortable-placeholder {
  background: #eee !important;
  visibility: visible !important;
  height: 10px !important;
  margin: 5px 0 !important;
  float: none !important;
  outline: none !important;
}
.keditor-section.ui-sortable-helper {
  background: #fff !important;
  opacity: 0.5;
}
